.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Replacment_Fields {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 10px;
  display: none;
}

.inputField {
  width: 500px;
  padding: 10px;
  border-bottom: 2px solid rgb(109, 106, 106);
  border-top: none;
  border-left: none;
  border-right: none;
  outline: none;
}
.Replacment_Fields button{
  width: 120px;
}
.flex{
  display: flex;
  justify-content: center;
  align-items: center;
}
.margin{
  margin-left: 45px;
}
.CountSpecific{
  display: flex;
  justify-content: center;
  align-items: center;
  display: none;
}
#countWord{
  width: 200px;
}
.TotalWords{
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  /* width: 50%; */
}
.TotalWords{
  border-bottom: 2px solid black;
}
#form{
  font-weight: 500;
}